import React from "react";
import { Table, Alert } from "react-bootstrap";
import { connect, useDispatch, useSelector } from "react-redux";

function Cart(props) {
  // useSelector를 사용해도 가능하다.
  let state = useSelector((state) => state);
  let dispatch = useDispatch();

  return (
    <div>
      <Table>
        <thead>
          <tr>
            <th>번호</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {state.reducer.map((data) => {
            return (
              <tr key={data.id}>
                <td>{data.id}</td>
                <td>{data.name}</td>
                <td>{data.quan}</td>
                <td>
                  <button
                    onClick={() => {
                      dispatch({ type: "increase", id: data.id });
                    }}
                  >
                    +
                  </button>
                  &nbsp;
                  <button
                    onClick={() => {
                      dispatch({ type: "decrease", id: data.id });
                    }}
                  >
                    -
                  </button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>

      {state.reducer2 ? (
        <Alert variant="warning">
          지금 구매하시면 20% 할인
          <button
            onClick={() => {
              dispatch({ type: false });
            }}
          >
            X
          </button>
        </Alert>
      ) : null}
    </div>
  );
}

// function loadData(state) {
