import React, { useState, useContext, lazy, Suspense } from "react";
import { Navbar, Container, Nav, NavDropdown, Button } from "react-bootstrap";
import "./App.css";
import data from "./data.js";
// import GoodDetail from "./Detail";
import { Link, Route, Switch } from "react-router-dom";
import axios from "axios";
import Cart from "./Cart.js";

let invenContext = React.createContext();
let GoodDetail = lazy(() => import("./Detail.js"));

function App() {
  let [goods, goodsModify] = useState(data);
  let [inven, invenModify] = useState([10, 11, 12]);

  return (
    <div className="App">
      <MainNavbar></MainNavbar>

      <Switch>
        <Route exact path="/">
          <div className="jumbotron">
            <div className="jumbo_container">
              <h1>X-MAS Sale Off</h1>
              <p>크리스마스 시즌 세일 10% ~ 50% 12.11 ~ 12.31</p>
              <p>
                <Button variant="light">세일중인 상품</Button>
              </p>
            </div>
          </div>

          <div className="goods_containter">
            <invenContext.Provider value={inven}>
              <div className="row">
                {goods.map((data, index) => {
                  return (
                    <GoodsComponent data={data} key={index}></GoodsComponent>
                  );
                })}
                ;
              </div>
            </invenContext.Provider>
            <button
              onClick={() => {
                axios
                  .get("https://codingapple1.github.io/shop/data2.json")
                  .then((receive) => {
                    goodsModify([...goods, ...receive.data]);
                  })
                  .catch(() => {
                    console.log("data not loaded.");
                  });
              }}
              className="btn btn-primary"
            >
              More
            </button>
          </div>
        </Route>

        <Route path="/detail/:id">
          <Suspense fallback={<div>Loading...</div>}>
            <GoodDetail
              invenModify={invenModify}
              inven={inven}
              goods={goods}
            ></GoodDetail>
          </Suspense>
        </Route>

        <Route path="/cart">
          <Cart></Cart>
        </Route>

        {/* <Route path="/:id">
          <div>
            "/id:" matching all path
            <br/>
            so if you want to match only one path,
            <br/>
            use 'Switch' tag provided by react-router-dom library
          </div>
        </Route> */}
      </Switch>
    </div>
  );
}

function MainNavbar() {
  return (
    <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="/">React-Bootstrap</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to="/">
              Home
            </Nav.Link>
            <Nav.Link as={Link} to="/detail/0">
              Detail
            </Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item>
                <Link to="/cart">Cart</Link>
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                Another action
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4 ">
                Separated link
              </NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

// 상품 list component(cards)
function GoodsComponent(props) {
  let refText = "/detail/" + props.data.id;
  let invenCont1 = useContext(invenContext);
  return (
    <div key={props.data.id} className="col-md-4">
      <a href={refText}>
        <img width="100%" src={props.data.img} alt={props.data.title} />
      </a>
      <h5>{props.data.title}</h5>
      <span className="price_block">
        <span className="price">{props.data.price}</span>
        <span className="won">원</span>
      </span>
      <Test></Test>
    </div>
  );
}

function Test() {
  let invenCont2 = useContext(invenContext);
  return (
    <div>
      <p>재고 : {invenCont2[0]} 개</p>
    </div>
  );
}

export default App;
