import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";
import { combineReducers, createStore } from "redux";
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";

// 1. 데이터를 변수에 저장(state)
let cartState = [
  { id: 4, name: "멋진신발", quan: 2 },
  { id: 5, name: "안멋진신발", quan: 6 },
  { id: 6, name: "좋은신발", quan: 1 },
];

let alertToggle = true;

function reducer2(state = alertToggle, action) {
  state = action.type ? true : false;
  return state;
}

// 2. 데이터를 수정하는 함수를 만듦(stateModify)
function reducer(state = cartState, action) {
  // action 파라미터를 통해 state가 수정되는 조건을 체크
  // 1. 넣으려는 id가 이미 장바구니에 존재할 경우, 수량만 증가시키도록
  // 2. 각 상품 별 주문하기 버튼 누르면 상품데이터 추가되도록 작성 OK
  if (action.type === "addCart") {
    let temp = state.findIndex((data) => data.id === action.payload.id);

    if (temp !== -1) {
      let copy = [...state];
      copy[temp].quan++;
      return copy;
    }

    let copy = [...state];
    copy.push({
      id: action.payload.id,
      name: action.payload.title,
      quan: action.inven,
    });
    return copy;
  }

  if (action.type === "increase") {
    let copy = [...state];
    let indexValue = copy.findIndex((c) => c.id === action.id);
    copy[indexValue].quan++;
    return copy;
  }

  if (action.type === "decrease") {
    let copy = [...state];
    let index = copy.findIndex((c) => c.id === action.id);
    copy[index].quan === 1 ? (copy[index].quan = 1) : copy[index].quan--;
    return copy;
  }

  return state;
}

// 3. stateModify를 createStore() 파라미터에 저장
let store = createStore(combineReducers({ reducer, reducer2 }));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
